import React from 'react';
import './Notes.css';

const Note = (props) => {
    return(
        <div className={'note'}>
            <li>
                <div>
                    <strong>{props.index + 1})&nbsp;{props.note.title}</strong>
                    <small>{props.note.date}</small>
                </div>
                <button onClick={()=> props.onNoteRemove(props.note.id)} className="button button3">&times;</button>
            </li>
        </div>
    )
}

export default Note;